.notification-container {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 999999;
  width: 400px;
  padding: 0px 15px;
  max-height: calc(100% - 30px);
  overflow-x: hidden;
  overflow-y: auto;
}

@media only screen and (max-width: 460px) {
  .notification-container {
    width: 100%;
  }
}

.notification {
  box-sizing: border-box;
  padding: 12px 15px 12px 10px;
  border-radius: 2px;
  color: #393939;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
  cursor: pointer;
  font-size: var(--base);
  position: relative;
  opacity: 0.9;
  margin-top: 16px;
  display: flex;
  align-items: center;
  transform: translate3d(100%, 0, 0);
}

.success .circle {
  stroke: #008d18;
  stroke-width: 2.5;
  fill: transparent;
  display: block;
}

.success .checkmark {
  stroke-dasharray: 0;
  stroke-dashoffset: 0;
  stroke: #008d18;
  stroke-width: 2.5;
  transform: rotate(-45deg);
  animation: none;
}

.info .circle {
  stroke: #1296c4;
  stroke-width: 2.5;
  fill: transparent;
  display: block;
}

.warning .circle {
  stroke: #e78c09;
  stroke-width: 2.5;
  fill: transparent;
  display: block;
}

.error .circle {
  stroke: #a0022d;
  stroke-width: 2.5;
  fill: transparent;
  display: block;
}

.error .remove-icon {
  stroke-dasharray: 0;
  stroke-dashoffset: 0;
  stroke: #a0022d;
  stroke-width: 2.5;
  animation: none;
}

.notification-bar {
  width: 6px;
  height: 60px;
  border-radius: 5px;
}

.notification-success .notification-bar {
  background-color: #008d18;
}

.notification-info .notification-bar {
  background-color: #1296c4;
}

.notification-warning .notification-bar {
  background-color: #e78c09;
}

.notification-error .notification-bar {
  background-color: #a0022d;
}

.notification-icon {
  margin: 0 14px;
}

.notification-message {
  width: calc(100% - 66px);
  /* display: inline-block; */
}

.notification .title {
  font-size: var(--lg);
  /* line-height: 1.2em; */
  font-weight: bold;
  margin: 0 0 5px 0;
}

.notification:hover,
.notification:focus {
  opacity: 1;
}

.notification-enter {
  visibility: hidden;
  transform: translate3d(100%, 0, 0);
}

.notification-enter.notification-enter-active {
  visibility: visible;
  transform: translate3d(0, 0, 0);
  transition: all 0.4s;
}

.notification-exit {
  visibility: visible;
  transform: translate3d(0, 0, 0);
}

.notification-exit.notification-exit-active {
  visibility: hidden;
  transform: translate3d(100%, 0, 0);
  transition: all 0.4s;
}

.notification-progress {
  position: absolute;
  width: 0;
  height: 3.5px;
  background: rgb(0, 0, 0);
  opacity: 0.4;
  transition: width 25ms;
  left: 0;
  bottom: 0;
}
