.wrapper {
  width: 100%;
  /* top: 200px;
  position: relative; */
}

.middleAns {
  width: 100%;
  height: 150px;
}

.history {
  position: relative;
  width: 80%;
  height: 400px;
  border-radius: 5px;
  margin: auto;
  margin-bottom: 5px;
  margin-top: 20px;
  background-color: var(--bg-white-200);
  overflow: auto;
}

.historyContent {
  height: 400px;
  padding: 10px;
  display: table-cell;
  vertical-align: bottom;
  width: 10%;
}

/* .historyContent > div:nth-child(odd) {
  display: flex;
  justify-content: flex-end;
  color: white;
} */

.hisAns {
  display: flex;
  color: white;
}

.hisQues {
  display: flex;
  justify-content: flex-end;
  color: white;
}

.historyContent > div {
  padding: 5px;
}

.hisQues img {
  width: 20px;
  height: 20px;
  vertical-align: middle;
  margin-left: 5px;
  cursor: pointer;
}

.hisQues > div {
  position: relative;
  width: fit-content;
  border-radius: 10px 10px 0px 10px;
  background-color: var(--bg-cyan);
  padding: 10px;
  margin-right: 60px;
}

.hisAns > div {
  position: relative;
  width: fit-content;
  border-radius: 0px 10px 0px 10px;
  background-color: var(--bg-pink);
  padding: 10px;
  margin-left: 60px;
}

.quesMark {
  position: absolute;
  top: -5px;
  right: -50px;
  display: flex;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  border: solid 2px var(--bg-cyan);
  /* background-color: white; */
  background-color: white;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  color: black;
}

.ansMark {
  position: absolute;
  top: -5px;
  left: -50px;
  display: flex;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  border: solid 2px var(--bg-pink);
  background-color: var(--secondary);
  padding: 5px;
  justify-content: center;
  align-items: center;
  font-weight: 700;
}
/* .historyContent div:nth-child(odd){
  float: right;
} */

.textarea_wrapper {
  width: 80%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  border-radius: 3px;
  overflow: hidden;
}

.answer {
  width: 100%;
  display: flex;
  justify-content: center;
  font-size: var(--x2xl);
  color: white;
  /* position: absolute;
  bottom: 570px; */
  animation: zoomans 0.5s linear;
  transform: scale(1);
}

.ans_content {
  width: 90%;
  max-height: calc(100vh - 480px);
  margin-bottom: 10px;
  overflow-y: auto;
  max-width: 1518px;
  text-align: center;
  line-height: 49px;
  padding: 20px !important;
  height: 150px;
}

/* width */
.ans_content::-webkit-scrollbar {
  width: 3px;
  background: transparent;
}

/* Track */
.ans_content::-webkit-scrollbar-track {
  background: transparent;
  opacity: 1;
}

/* Handle */
.ans_content::-webkit-scrollbar-thumb {
  background: #46bdc3;
  border-radius: 2px;
}

.ans_content::-webkit-scrollbar-track-piece:start {
  /* background: transparent; */
  background-color: var(--secondary);
  opacity: 0.5;
}

.ans_content::-webkit-scrollbar-track-piece:end {
  /* background: transparent; */
  background-color: var(--secondary);
  opacity: 0.5;
}

@keyframes zoomans {
  from {
    opacity: 0.5;
    transform: scale(0.5, 0.5);
  }
  to {
    opacity: 1;
    transform: scale(1, 1);
  }
}

.textarea_wrapper textarea {
  width: 100%;
  height: 91px;
  /* max-height: 270px; */
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
  font-size: var(--x3xl);
  padding: 15px;
  border: none;
  border-radius: 0px !important;
}

@media only screen and (max-width: 1080px) {
  .textarea_wrapper textarea {
    font-size: var(--xxxl);
    height: 68px;
  }
}

@media only screen and (max-width: 600px) {
  .textarea_wrapper textarea {
    font-size: var(--xxl) !important;
    padding: 10px !important;
    height: 48px;
  }
  textarea::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    font-size: var(--lg) !important;
  }
  textarea::-moz-placeholder {
    /* Firefox 19+ */
    font-size: var(--lg) !important;
  }
  textarea:-ms-input-placeholder {
    /* IE 10+ */
    font-size: var(--lg) !important;
  }
  textarea:-moz-placeholder {
    /* Firefox 18- */
    font-size: var(--lg) !important;
  }
  .answer {
    font-size: var(--mxxl);
    bottom: 360px;
  }
  .ans_content {
    line-height: 26px;
    padding: 10px !important;
  }
  .btn {
    height: 40px !important;
    width: 100px !important;
    font-size: var(--lg) !important;
  }
}

.btn_wrapper {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  width: 100%;
  display: none;
}

.btn {
  height: 50px;
  width: 140px;
  background-color: #674a7d;
  outline: none;
  border-radius: 3px;
  border: none;
  font-size: var(--mxl);
  color: rgba(255, 255, 255, 0.7);
}

.btn:hover {
  background-color: #5c3f72;
}

.btn:active {
  box-shadow: 1px 1px 1px #5c3f72;
}

.quesArea {
  background-color: var(--bg-white-200);
  resize: none;
}

.copier {
  position: absolute;
  right: 10000px;
}

.copyAlert {
  position: absolute;
  top: -10px;
  right: -50px;
  background-color: rgb(151, 144, 144);
  padding: 5px;
  color: white;
  font-weight: 600;
  font-size: var(--xs);
  border-top-left-radius: 50% 50%;
  border-top-right-radius: 50% 50%;
  border-bottom-left-radius: 50% 50%;
  border-bottom-right-radius: 50% 50%;
}

.ansMark img {
  width: 25px;
  height: 25px;
  margin: 0px !important;
}

.progressWrapper {
  width: 100%;
  height: 3px;
  background-color: var(--bg-white-200);
}

.progress {
  width: 0%;
  height: 100%;
  background-color: #46bdc3;
  transition: 0.3s;
}

@media screen and (max-width: 600px) {
  .history {
    height: 300px;
    margin-top: 50px;
  }

  .historyContent {
    height: 300px;
  }
}
