.wrapper {
  width: 170px;
  height: 71px;
  display: flex;
  justify-content: space-between;
}

.btn {
  width: 70px;
  height: 70px;
  border-radius: 50px;
  margin-left: 15px;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn:hover {
  cursor: pointer;
}

.innerbtn {
  width: 52px;
  height: 52px;
  border-radius: 50px;
  background-color: rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
}

.voice {
  width: 100%;
  height: 100%;
}
.unclicked {
  border: solid 2px white;
}

.clicked {
  border: solid 2px #58ecff;
  box-shadow: 0px 0px 7px #58ecff, inset 0px 0px 2px #58ecff;
}

.clicked > div {
  background-color: rgba(88, 236, 255, 0.7);
  box-shadow: 0px 0px 9px rgba(88, 236, 255, 0.7);
}

@media only screen and (max-width: 767px) {
  .wrapper {
    margin: auto;
  }
}
