#question-placeholder {
  font-size: 40px;
  font-family: inherit;
  text-align: center;
  line-height: 60px;
  color: #fff;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  /* position: relative;
  top: 200px; */
}

@media only screen and (max-width: 1100px) {
  #question-placeholder {
    width: 100%;
  }
}

#microphone {
  width: 150px;
  height: 150px;
  margin: 100px auto 0;
  display: flex;
  justify-content: center;
}

@media only screen and (max-width: 767px) {
  #microphone {
    margin: 75px auto 0;
  }
}

#btn-speak {
  background: transparent;
  width: 100px;
  height: 100px;
  border: 2px solid var(--bg-white-500);
  border-radius: 50%;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  outline: none;
}

#btn-speak svg {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  border: 3px solid transparent;
  background: var(--bg-white-500);
}

#btn-speak svg > path:first-child {
  fill: var(--bg-white-500);
}

#btn-speak svg.recording > path:first-child {
  fill: #a0e6f7;
}

#btn-speak svg > path:last-child {
  fill: none;
}

@keyframes border-pulsate {
  0%,
  100% {
    border-color: #a0e6f7;
  }

  50% {
    border-color: rgba(255, 0, 0, 0);
  }
}

@keyframes cross-pulsate {
  0%,
  100% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }
}

.old_answer {
  animation-name: becomeold;
  animation-duration: 0.7s;
  /* position: relative; */
  top: -50px;
  font-size: var(--x3xl);
  opacity: 0.5;
  transform: scale(0.7, 0.7);
  max-height: calc(100vh - 480px);
  overflow: auto;
  line-height: 70px;
}

#question-placeholder .popup {
  animation-name: becomePopUp;
  animation-duration: 0.7s;
  /* position: relative; */
  top: -50px;
  font-size: var(--x3xl);
  transform: scale(1, 1);
  width: 90%;
  overflow-y: auto;
  max-width: 1518px;
  text-align: center;
  line-height: 49px;
  padding: 20px;
  margin: auto;
  max-height: calc(100vh - 480px);
}

@keyframes becomeold {
  from {
    opacity: 1;
    top: 100px;
    transform: scale(1);
  }

  to {
    opacity: 0.5;
    top: -50px;
    transform: scale(0.7);
  }
}
@keyframes becomePopUp {
  from {
    opacity: 0.5;
    top: 100px;
    transform: scale(0.7);
  }

  to {
    opacity: 1;
    top: -60px;
    transform: scale(1);
  }
}

@media only screen and (max-width: 768px) {
  .old_answer {
    font-size: var(--mxxl);
    line-height: 32px;
  }
  #question-placeholder .popup {
    font-size: var(--mxxl);
    line-height: 26px;
    padding: 10px;
    max-height: calc(100vh - 480px);
  }
}
