.qna {
  min-height: calc(100vh - 380px);
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.qna .loading-answer {
  display: inline-block;
  width: 168px;
  height: 168px;
  top: 50px;
  margin: auto;
  position: absolute;
  left: calc(50vw - 84px);
}

@media only screen and (max-width: 600px) {
  .qna .loading-answer {
    top: 100px;
  }
}

.qna .initial-message {
  font-size: var(--x2xl);
  font-family: inherit;
  text-align: center;
  line-height: 60px;
  color: #fff;
  padding: 0 20px;
}

@media only screen and (max-width: 460px) {
  .qna .initial-message {
    font-size: var(--mxxl);
    line-height: 40px;
    top: 150px;
  }
}

.qna .interaction-mode {
  position: relative;
  display: block;
  margin: 100px auto 50px;
  width: 250px;
  box-sizing: content-box;
  border-radius: 35px;
  padding: 10px 0;
  height: 100px;
}

.qna .interaction-mode.toggle {
  width: 100px;
}

@media only screen and (max-width: 460px) {
  .qna .interaction-mode {
    margin: 60px auto 40px;
  }
}

.rotate-180 {
  transform: rotateY(180deg);
}

.qna .interaction-mode .microphone-wrapper,
.qna .interaction-mode .typing-wrapper {
  position: absolute;
  transition: margin-left 0.5s;
}

.qna .interaction-mode .microphone-wrapper {
  margin-left: 0;
}

/* .qna .interaction-mode .microphone-wrapper.on {
    margin-left: 75px;
}  */

.qna .interaction-mode .microphone-wrapper.off {
  visibility: hidden;
}

.qna .interaction-mode .typing-wrapper {
  margin-left: 150px;
}

/* .qna .interaction-mode .typing-wrapper.on {
    margin-left: 75px;
}  */

.qna .interaction-mode .typing-wrapper.off {
  visibility: hidden;
}

.qna #microphone,
.qna #typing {
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  margin: 0;
}

.qna .btn-speak,
.qna .btn-typing {
  background: transparent;
  width: 100px;
  height: 100px;
  border: 2px solid var(--bg-white-500);
  border-radius: 50%;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  outline: none;
}

.qna .btn-speak svg.recording > path:first-child,
.qna .btn-typing svg.typing > path:first-child {
  fill: #a0e6f7;
}

.qna .btn-speak.enabled,
.qna .btn-typing.enabled {
  border-color: #a0e6f7;
}

.qna .btn-speak svg,
.qna .btn-typing svg {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  border: 2px solid transparent;
  background: var(--bg-white-500);
  padding: 8px;
}

.qna .btn-speak svg > path:first-child,
.qna .btn-typing svg > path:first-child {
  fill: var(--bg-white-500);
}

@keyframes border-pulsate {
  0%,
  100% {
    border-color: #a0e6f7;
  }

  50% {
    border-color: rgba(255, 0, 0, 0);
  }
}

@keyframes cross-pulsate {
  0%,
  100% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }
}

.qna .slider-container-wrapper {
  width: 900px;
  margin: 0 auto;
  padding: 50px 50px 60px;
}

.qna .slider-container {
  position: relative;
  padding: 10px 0;
}

@media only screen and (max-width: 1024px) {
  .qna .slider-container-wrapper {
    width: 100%;
    padding: 50px 50px 60px;
  }
}

.qna .slider {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 25px;
  background: rgba(255, 255, 255, 0.05);
  outline: none;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}

.qna .slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  background: var(--bg-primary);
  cursor: pointer;
}

.qna .slider-container .min-text,
.qna .slider-container .min-text-mobile,
.qna .slider-container .max-text,
.qna .slider-container .max-text-mobile {
  position: absolute;
  color: var(--bg-white-800);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: var(--mxl);
}

@media only screen and (max-width: 560px) {
  .qna .slider-container .min-text,
  .qna .slider-container .max-text {
    display: none;
  }
}

.qna .slider-container .min-text-mobile,
.qna .slider-container .max-text-mobile {
  display: none;
}

@media only screen and (max-width: 560px) {
  .qna .slider-container .min-text-mobile,
  .qna .slider-container .max-text-mobile {
    display: block;
  }
}

.qna .slider-container .min-text,
.qna .slider-container .min-text-mobile {
  bottom: 100%;
  left: 0;
}

.qna .slider-container .max-text,
.qna .slider-container .max-text-mobile {
  bottom: 100%;
  right: 0;
}

.qna .popup-header {
  font-size: var(--mxxl) !important;
}

@media only screen and (max-width: 1300px) {
  .qna {
    min-height: calc(100vh - 345px);
  }
}

@media only screen and (max-width: 767px) {
  .qna {
    min-height: calc(100vh - 310px);
  }
}
