:root {
  --xs: 10px;
  --sm: 12px;
  --base: 14px;
  --lg: 16px;
  --xl: 18px;
  --mxl: 20px;
  --xxl: 22px;
  --mxxl: 24px;
  --xxxl: 30px;
  --x2xl: 45px;
  --x3xl: 50px;
  --bg-primary: #131c31;
  --bg-primary-blur: #19253d;
  --bg-secondary-blur: #20263e;
  --bg-primary-200: rgba(19, 28, 49, 0.2);
  --bg-primary-500: rgba(19, 28, 49, 0.5);
  --bg-primary-900: rgba(19, 28, 49, 0.9);
  --bg-pink: #773195;
  --bg-cyan: #33cccc;
  --secondary: #102047;
  --strengthen: #08163a;
  --bg-white-200: rgba(255, 255, 255, 0.2);
  --bg-white-350: rgba(255, 255, 255, 0.35);
  --bg-white-500: rgba(255, 255, 255, 0.5);
  --bg-white-800: rgba(255, 255, 255, 0.8);
  --bg-white-900: rgba(255, 255, 255, 0.9);
  --text-white-650: rgba(255, 255, 255, 0.65);
  --text-white-250: rgba(255, 255, 255, 0.25);
  --error: #bb2121;
  --bg-gray: #707070;
}

.alpha {
