.loading-placeholder {
  position: absolute;
  height: 100vh;
  width: 100%;
}

.lds-wrapper {
  position: absolute;
  /* top: calc(50% - 12px);
  left: calc(50% - 25px); */
  top: 0px;
  left: 0px;
  display: flex;
  align-items: center;
  background-color: #ffffff05;
  z-index: 995;
  /* width: 50px;
  height: 24px; */
  width: 100%;
  height: 100%;
}

.loader,
.loader:before,
.loader:after {
  background: var(--bg-white-500);
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 5px;
  height: 24px;
}
.loader {
  color: var(--bg-white-500);
  text-indent: -9999em;
  margin: 0 auto;
  position: relative;
  font-size: var(--xs);
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: "";
}
.loader:before {
  left: -10px;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 10px;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 24px;
  }
  40% {
    box-shadow: 0 -2em;
    height: 34px;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 24px;
  }
  40% {
    box-shadow: 0 -2em;
    height: 34px;
  }
}

/* width */
.common-scroller::-webkit-scrollbar {
  width: 3px;
  background: transparent;
}

/* Track */
.common-scroller::-webkit-scrollbar-track {
  background: transparent;
  opacity: 1;
}

/* Handle */
.common-scroller::-webkit-scrollbar-thumb {
  background: #46bdc3;
  border-radius: 2px;
}

.common-scroller::-webkit-scrollbar-track-piece:start {
  /* background: transparent; */
  background-color: var(--secondary);
  opacity: 0.5;
}

.common-scroller::-webkit-scrollbar-track-piece:end {
  /* background: transparent; */
  background-color: var(--secondary);
  opacity: 0.5;
}

