.useranswer {
  position: relative;
  border: solid 2px #58ecff;
  border-radius: 50px;
  display: flex;
  width: 90px;
  height: 90px;
  justify-content: center;
  margin: auto;
}

.innericon {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 26px;
  width: 70px;
  box-shadow: none !important;
}

.inner {
  width: 15px;
  height: 15px;
  border-radius: 20px;
  background-color: #58ecff;
}

.connector {
  width: 33px;
  border: solid 1px #58ecff;
}

.inneranswer {
  width: inherit;
  height: inherit;
  border-radius: 50%;
  box-shadow: 0px 0px 10px 2px #58ecff, inset 0px 0px 10px 2px #58ecff !important;
  border: none !important;
  position: relative;
  display: flex;
  justify-content: center;
}

.soffosanswer {
  position: relative;
  border: solid 2px #58ecff;
  border-radius: 70px;
  display: flex;
  width: 110px;
  height: 110px;
  justify-content: center;
  margin: auto;
  top: -10px;
  box-shadow: 0px 0px 6px 2px #58ecff, inset 0px 0px 3px 1px #58ecff;
}

.soffosanswer div,
.lds_dual_ring div {
  box-shadow: 0px 0px 5px 2px #58ecff, inset 0px 0px 3px 1px #58ecff;
}

.lds_dual_ring:after {
  top: -112px;
  left: 16px;
  position: relative;
  content: " ";
  display: block;
  width: 110px;
  height: 110px;
  margin: 8px;
  border-radius: 50%;
  border: 4px solid #58ecff;
  border-color: #58ecff transparent #58ecff transparent;
  animation: lds-dual-ring 1.2s linear infinite;
  box-shadow: 0px 16px 12px -16px #58ecff, 0px -16px 12px -16px #58ecff,
    inset 0px 16px 2px -14px #58ecff, inset 0px -16px 2px -14px #58ecff;
}

.soffossleep {
  position: relative;
  border: solid 2px #58ecff;
  border-radius: 50px;
  display: flex;
  width: 90px;
  height: 90px;
  justify-content: center;
  margin: auto;
}

/* .soffossleep > div > div {
  height: 10px;
  width: 15px;
  border-radius: 0 0 15px 15px;
} */

.soffossleep .inner {
  height: 10px;
  width: 15px;
  border-radius: 0 0 15px 15px;
}

.soffossleep .connector {
  border: unset;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 600px) {
  .soffosanswer {
    width: 80px;
    height: 80px;
  }
  .useranswer {
    width: 65px;
    height: 65px;
  }
  .innericon {
    top: 18px;
    width: 45px;
  }
  .inner {
    width: 10px;
    height: 10px;
  }
  .connector {
    width: 25px;
  }
  .lds_dual_ring:after {
    top: -82px;
    left: 33px;
    width: 85px;
    height: 85px;
    border: 4px;
  }
}
