.footer {
  width: 1440px;
  display: flex;
  margin: auto;
  align-items: center;
  justify-content: space-evenly;
}

.footer_wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.private_folders {
  width: 90px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: var(--mxl);
  color: white;
}

.private_folders p {
  width: 150px;
}

.waveform {
  width: 1000px;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.waveform canvas {
  width: 95%;
  height: 100%;
}

.mobile_question {
  display: none;
}

@media only screen and (max-width: 1440px) {
  .footer {
    width: 1220px;
  }
}

@media only screen and (max-width: 1280px) {
  .footer {
    width: 920px;
  }
}

@media only screen and (max-width: 960px) {
  .footer {
    width: 720px;
  }
}

@media only screen and (max-width: 767px) {
  .footer {
    width: 100%;
    display: block;
    bottom: 30px;
  }
  .mobile_question {
    display: block;
  }
  .web_question {
    display: none;
  }
  .waveform canvas {
    width: 100%;
    height: 100%;
  }
  .waveform {
    height: 100px;
    width: 100%;
  }
  .private_folders {
    margin: auto;
    margin-top: 5px;
    width: 255px;
  }
}
